  transition: opacity .3s ease;
}
.modal-wrapper[data-v-10fb102f] {
  display: table-cell;
  vertical-align: middle;
}
.modal-container[data-v-10fb102f] {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}
.modal-title[data-v-10fb102f]{
  color: #beb5fc;
}
.modal-enter .modal-container[data-v-10fb102f],
.modal-leave-active .modal-container[data-v-10fb102f] {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
img[data-v-10fb102f]{
  width: 100%;
}
.modal-close-button[data-v-10fb102f]{
  text-decoration: none;
  color: #beb5fc;
  padding: 5px;
  border-radius: 5px;
  border: solid 2px #beb5fc;
