  vertical-align: middle;
  overflow: hidden;
  font-weight: bold;
  -webkit-transition: .4s;
  transition: .4s;
  cursor: pointer;
}
.modal-close-button[data-v-10fb102f]:hover{
  background: #d2cafc;
}

